.shrink-fade-side-in {
  transform: translate(1rem, -1rem) scale(1.618);
  opacity: 0.5;
  animation: shrinkFadeSideIn 0.4s $springEase forwards;
}

@keyframes shrinkFadeSideIn {
  from {
    opacity: 0.5;
    transform: translate(1rem, -1rem) scale(1.618);
  }

  to {
    opacity: 1;
    transform: translate(0rem, 0rem) scale(1);
  }
}

