.type-in-effect {
  position: relative;

  &>.placeholder {
    visibility: hidden;
    user-select: none;
    white-space: nowrap;
  }

  &>.characters {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    white-space: nowrap;
    text-align: left;
  }
}